<template>
  <div id="shop">
    <mobile-header
      right-icon="mdi-share-variant-outline"
      title="근조화환"
      class="reg-obituary"
    />

    <nav>
      <ul class="list">
        <li
          v-for="(type, idx) in getShopTypeList"
          :key="idx"
          class="category"
          :class="{ active: shopType === type.id }"
          @click="changeType(type)"
        >
          {{ type.shopClassCodeTitle }}
        </li>
      </ul>
    </nav>

    <main>
      <template>
        <!-- v-if="type === product.type" -->
        <div class="product-card__wraps">
          <!-- <div v-if="!product.list">주문 가능한 상품이 없습니다</div> -->

          <ProductCard
            v-for="(shop, productId) in getShopList"
            :key="productId"
            :product="shop"
          />
        </div>
      </template>

      <!-- <span class="goods_telorder" style="position: relative; "
            ><a style="color: #222" class="pb-4" href="tel:1688-2734"
              >전화로 주문하기</a
            ></span
          > -->
      <ShopFooter />
    </main>
  </div>
</template>

<script>
import ShopFooter from "@/components/common/ShopFooter";
import myMixin from "@/mixin/mixin";
import { mapGetters } from "vuex";
import MobileHeader from "../../components/common/MobileHeader.vue";
import ProductCard from "../../components/shop/ProductCard.vue";
export default {
  name: "Shop",
  mixins: [myMixin],
  components: {
    ShopFooter,
    MobileHeader,
    ProductCard
  },
  data() {
    return {
      shopDetail: {
        id: null,
        shopTitle: "",
        shopTxt: "",
        shopPrice: 0,
        shopDc: 0,
        shopType: {
          codeSubId: null
        },
        shopFile: [],
        shopReply: [],
        shopQna: []
      },
      shopTypeName: "",
      shopType: 1,
      EcodeId: this.$route.query.EcodeId ? this.$route.query.EcodeId : null
    };
  },
  mounted() {
    if (this.EcodeId !== null) {
    } else {
      alert("문제가 발생하였습니다. 확인후 다시 시도해 주세요.");
      this.$router.go(-1);
      return;
    }
    const parameter = {
      context: this
    };
    this.$store.dispatch("REQUEST_SHOP_TYPE", parameter);
    //this.requstShopList()
  },
  computed: {
    ...mapGetters(["getShopList", "getShopTypeList", "getShopUse"])
  },
  methods: {
    imagePath(shop) {
      if (shop.shopFile !== null && shop.shopFile.length > 0) {
        return shop.shopFile[0].filePath;
      } else {
        return "";
      }
    },
    computedPrice2(shop) {
      return shop.shopPrice * (1 - shop.shopDc / 100);
    },
    changeType(type) {
      this.shopTypeName = type.shopClassCodeTitle;
      this.shopType = type.id;
      this.requstShopList();
    },
    requstShopList() {
      this.$store.commit("INIT_SHOP_LIST");
      const parameter = {
        page: false,
        shop: this.shopDetail,
        shopType: this.shopType,
        EcodeId: this.EcodeId
      };
      this.$store.dispatch("REQUEST_SHOP_LIST", parameter);
    },

    detailShop(shop) {
      if (this.getShopUse === "none") {
        return;
      }

      this.$router.push({
        path: "/shopDetail",
        query: {
          id: shop.id,
          EcodeId: this.$route.query.EcodeId ? this.$route.query.EcodeId : null
        }
      });
    }
  }
};
</script>
