<template>
  <div class="info__wraps">
    <p>{{ product.shopTitle }}</p>
    <span><font> 13%</font> <del>84,900원</del></span>

    <h4>
      <span class="cost" v-if="product.shopDc !== 0"
        >{{ comma(product.shopPrice) }}원</span
      >
      <span class="price"
        >{{ comma(computedPrice2(product))
        }}<strong style="font-size:14px;">원</strong></span
      >
    </h4>
  </div>
</template>

<script>
import myMixin from "@/mixin/mixin";
export default {
  props: {
    product: {
      type: Object,
      default: Object
    }
  },
  mixins: [myMixin],
  methods: {
    computedPrice2(shop) {
      return shop.shopPrice * (1 - shop.shopDc / 100);
    }
  }
};
</script>

