<template>
  <div class="product-card" @click="detailShop(product)">
    <!-- {{ product }} -->
    <div class="image__wraps">
      <img
        v-show="loadingImg"
        class="product-image"
        :src="imagePath(product)"
        alt=""
        @load="loadingImg"
      />
      <v-skeleton-loader
        v-if="!imgLoading"
        max-width="100%"
        height="100%"
        type="image"
      >
      </v-skeleton-loader>
    </div>

    <section>
      <ChipBox />
      <InfoBox :product="product" />
    </section>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import ChipBox from "./ChipBox.vue";
import InfoBox from "./InfoBox.vue";
export default {
  components: { ChipBox, InfoBox },
  props: {
    product: {
      type: Object,
      default: Object
    }
  },
  data() {
    return {
      imgLoading: false
    };
  },
  computed: {
    ...mapGetters(["getShopUse"])
  },
  mounted() {},
  methods: {
    loadingImg() {
      this.imgLoading = true;
    },
    imagePath(shop) {
      if (shop.shopFile !== null && shop.shopFile.length > 0) {
        return shop.shopFile[0].filePath;
      } else {
        return "";
      }
    },
    detailShop(product) {
      if (this.getShopUse === "none") {
        return;
      }

      this.$router.push({
        path: "/shopDetail",
        query: {
          id: product.id,
          EcodeId: this.$route.query.EcodeId ? this.$route.query.EcodeId : null
        }
      });
    }
  }
};
</script>

